@import url(https://fonts.googleapis.com/css2?family=Rowdies&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@500&family=Roboto+Slab&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
    background-color: #252934;
}

#headerbg {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-image: url("https://images.unsplash.com/photo-1448375240586-882707db888b?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjU1NzQ5fQ&w=1000&q=80");
    width: 100%;
    height: 25em;
}

#profilepic {
    position: absolute;
    background-image: url("/Portfolio-Paul/static/media/profile 1.3122bce6.png");
    border: 2.5px solid white;
    border-radius: 50%;
    width: 200px;
    height: 200px;
    background-repeat: no-repeat;
    object-fit: cover;
}

#profilepic:hover {
    background-image: url("/Portfolio-Paul/static/media/profile 2.a15aea65.jpg");
    background-repeat: no-repeat;
    object-fit: cover;
}

#profilediv {
    text-indent: -50%;
}

#navlinkdiv {
    display: flex;
    flex-direction: row;
    width: 75vw;
    height: 2.5em;
    margin-top: auto;
    background-color: #252934c4;
    transition: 0.4s ease;
}

.navs {
display: flex;
justify-content: center;
align-items: center;
border: 3px solid rgb(255, 255, 255);
width: 25em;
height: 1.5em;
text-decoration: none;
}

.navs:hover {
background-color: rgba(255, 255, 255, 0.363);
transition: ease 0.5s;
}

.navs {
font-size: 1.5em;
font-family: 'Rowdies', cursive;
}
.navs > p {
    color: white;
    text-decoration: none;
}
.navs > p:hover {
    color: #e31b6d;
    text-shadow: 2px 2px #252934;
    transition: 0.2 ease;
}

#hello {
    color: white;
    margin-top: auto;
    z-index: 100000;
    margin-bottom: 10em;
    text-shadow: 3px 3px black;
}

mark {
    color: #e31b6d;
    background: none
}
div {
    margin-left: auto;
    margin-right: auto;
}

h2 {
    color: #252934;
}

p {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: #e31b6d;
    font-family: 'Noto Sans TC', sans-serif;
    font-weight: lighter;
}


#bigbadgebox {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: white;
    border: 4px solid #04c2c9;
    width: 75%;
}

.flavortext {
    font-size: 1em;
}


.blinking{
    -webkit-animation:blinkingText 1.2s infinite;
            animation:blinkingText 1.2s infinite;
    text-decoration: underline;
    font-weight: bold;
}
@-webkit-keyframes blinkingText{
    0%{     color: #252934;    }
    49%{    color: #252934; }
    60%{    color: transparent; }
    99%{    color:transparent;  }
    100%{   color: #252934;    }
}
@keyframes blinkingText{
    0%{     color: #252934;    }
    49%{    color: #252934; }
    60%{    color: transparent; }
    99%{    color:transparent;  }
    100%{   color: #252934;    }
}


